import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    allProducts: {
      F1: {
        title: '領結長袖針織衫',
        gender: '女',
        price: 490,
        sizes: [
          {
            name: 'S',
            num: 1
          },
          {
            name: 'M',
            num: 2
          },
          {
            name:'L',
            num:3
          },
        ]
      }
    },
    cart: [],
    isAddToCart: false,
    asideData: {
      activity: {
      },
      women: [
        {
          title: "上身類",
          url: "tshirt",
          subCategory: [
            {
              name: "印花短T",
              url: "g-st"
            },
          ]
        },
      ],
    },
    checkStep: 0,
    totalPrice: 0,
    receiverData: {
    },
    payType: '',
  },
  mutations: {
    ADDTOCART(state, data) {
      state.cart.push(data);
      state.isAddToCart = true;
      setTimeout(function() {
        state.isAddToCart = false
      },1000)
    },
    CHECKSTEP(state, stepNum){
      state.checkStep = stepNum
    },
    CHECKCART(state,data){
      state.cart = data
    },
    TOTALPRICE(state,num){
      state.totalPrice = num
    },
    SETRECEIVE(state,data){
      state.receiverData = data
    },
    SETPAYTYPE(state, data){
      state.payType = data
    }
  },
  actions: {},
});
